.none {
  content: '//- CATAGORY LIST //-';
}

.categoryStart {
  /* display: grid;
  grid-template-rows: repeat(17, minmax(2.1rem, 5.4vh)); */
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding-top: 0.5rem;
  padding-right: 0.5rem;
  padding-left: 0.5rem;
  /* height: 5.4vh; */
  /* min-height: 2.1rem; */
  height: 80vh;
  width: 100%;
  overflow-y: auto;
}

/* .categoryStart::-webkit-scrollbar {
  display: none;
} */
.categoryStart::-webkit-scrollbar {
  width: 0.4rem;
  position: absolute;
}

/* .categoryStart::-webkit-scrollbar-track { */
/* background-color: #3C3D3F; */
/* background: linear-gradient(to bottom, var(--main-background-color) 0, var(--main-background-color) 3.12rem, #3c3d3f 3.12rem); */
/* } */

.categoryStart::-webkit-scrollbar-thumb {
  background: linear-gradient(to top, #3c3d3f00, var(--button-background-color-linear), #3c3d3f00);
}

.categoryStart::-webkit-scrollbar-thumb:hover {
  background: #656566;
}

.tabCat {
  position: relative;
  background-color: #fff;
  display: flex;
  /* flex-grow: 1; */
  /* border-bottom: #656667 solid 1px; */
  transition: all 0.2s;
  box-shadow: 0.5px 0.5px 8px rgba(0, 0, 0, 0.35);
  border-radius: 0.7rem;
}

.newCatBtn {
  font-size: 0.9rem;
  padding: 0.3rem 0.5rem;
  color: #eee;
  border: #cab7f7 1px solid;
  box-shadow: 0.1rem 0.1rem 0.4rem rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  /* border-radius: .1rem; */
  white-space: nowrap;
}

.newCatBtn:hover {
  transition: all 0.2s;
  transform: scale(1.02);
  /* transform: translateY(-5%) */
  /* border: #ffffff 2px solid; */
  box-shadow: 0.2rem 0.2rem 0.4rem rgba(0, 0, 0, 0.3);
  /* box-shadow: .1rem .1rem .4rem rgba(255, 255, 255, 0.3); */
}
.catSaveCancelRemove {
  display: flex;
  flex-direction: column;
  gap: .5rem;
  align-items: flex-end;
}
.catSaveCancel {
  /* grid-row: 13/14; */
  /* transform: translateY(50%); */
  padding-right: 0.5rem;
  display: flex;
  gap: 0.2rem;
  /* justify-content: space-between; */
  /* align-items: center; */
  /* padding: 0 0.2rem; */

  /* padding-right: 0.5rem;
  padding-left: 0.5rem; */
}

.smallMainBtnX {
  min-width: 5rem;
  gap: 0.7rem;
}

.xCatSave {
  gap: 0.7rem;
  min-width: none;
  flex-grow: 1;
}

.newCatBtn:active {
  box-shadow: 0.1rem 0.1rem 0.4rem rgba(0, 0, 0, 0.3);
  transform: scale(0.98);
  transition: all 0.2s;
}

.mini:hover {
  /* background-color: #4b4b4c; */
  background: linear-gradient(to right top, var(--button-background-color), var(--button-background-color-linear));
  box-shadow: 0.1rem 0.1rem 0.5rem var(--button-shadow);
  transform: scale(1.01);
  transform-origin: right;
}

/* .tabCat:active {
  background-color: #4b4b4c;
} */
.chooseCat {
  /* background-color: #656667; */
  /* border: #aaa 0.5px solid; */

  background: linear-gradient(to right top, var(--button-background-color), var(--button-background-color-linear));
  box-shadow: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.35);
  transform: scale(1.03);
  transform-origin: right;
  color: #fff;
}
.btnCat {
  display: flex;
  flex-grow: 1;
  font-size: 1.1rem;
  /* color: var(--cat-font-color); */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0.5rem;
}
