}

.langBtnL {
  width: 2rem;
  color: #aaa;

  /* height: 100%; */
}

.langBtnL:hover {
  color: #fff;
}

.iconSideBox {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100%;
}

.deleteBtn {
  background-color: #000000;
  border-left: #656667 solid 1px;
  color: #eee;
  width: 3rem;
  height: 100%;
}

.deleteBtn:hover {
  border-left: #656667 solid 1px;
  color: #cb2722;
  width: 3rem;
  height: 100%;
}

.dispBox {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s;
}

.dispNone {
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
}

.hidden {
  display: none;
}

